<template>
  <li>
    <label>
      <!--<input type="checkbox" :checked="todo.done" />-->
      <!--<input type="checkbox" v-model="todo.done" />-->
      <input type="checkbox" :checked="todo.done" @change="handleCheck(todo.id)" />
      
      <span>{{todo.title}}</span>
    </label>
    <button class="btn btn-danger" style="display: black" @click="handDelete(todo.id)">删除</button>
  </li>
</template>

<script>
export default {
  name: "Item",
  //声明接收todo对象
  props:['todo','delTodo','checkTodo'],
  methods:{
    handleCheck(id){
      //通知App组件将对应的todo对象的done值取反
      this.checkTodo(id)
    },
    handDelete(id){
      if(confirm('确定删除吗？')){
        this.delTodo(id)
      }
    }
  }
};
</script>

<style scoped>
    li{
        list-style: none;
        height: 36px;
        line-height: 36px;
        padding: 0 5px;
        border-bottom: 1px solid #ddd;
    }
    li label{
        float:left;
        cursor: pointer;
    }
    li button{
      float: right;
      display: none;
      margin-top: 3px;
    }
    li label li input{
        vertical-align: middle;
        margin-right: 6px;
        position: relative;
        top:-1px;
    }
    li:before{
        content:initial;
    }
    li:last-child{
        border-bottom: none;
    }
    li:hover{
      background-color:#ddd
    }
    li:hover button{
      display: block;
    }
</style>