<template>
  <div class="todo-footer" v-show="total">
    <label>
      <!--<input type="checkbox" :checked="isAll" @change="handAll" />-->
      <input type="checkbox" v-model="isAll" />
    </label>
    <span>
      <span>已完成{{ tempTodo }}</span> / 全部{{ total }}
    </span>
    <button class="btn btn-danger" @click="handClear">清除已完成任务</button>
  </div>
</template>

<script>
export default {
  name: "MyFooter",
  props: ["todos", "clearTodo", "allDone"],
  methods: {
    handClear() {
      confirm('确认清除已完成任务？')?this.clearTodo():0
    },
  },
  computed: {
    total() {
      return this.todos.length;
    },
    tempTodo() {
      /*
      return this.todos.reduce((pre, current) => {
        return pre + (current.done ? 1 : 0);
      }, 0);*/
      //下面是简写
      return this.todos.reduce(
        (pre, current) => pre + (current.done ? 1 : 0),
        0
      );
    },
    isAll: {
      get() {
        return this.total == this.tempTodo && this.total > 0;
      },
      set(value) {
        this.allDone(value);
      },
    },
  },
};
</script>

<style scoped>
.todo-footer {
  height: 40px;
  line-height: 40px;
  padding-left: 6px;
  margin-top: 5px;
}
.todo-footer label {
  display: inline-block;
  margin-right: 20px;
  cursor: pointer;
}
.todo-footer label input {
  position: relative;
  top: -1px;
  vertical-align: middle;
  margin-right: 5px;
}
.todo-footer button {
  float: right;
  margin-top: 5px;
}
</style>