<template>
  <div>
    <div class="todo-container">
      <div class="todo-wrap">
        <MyHeader :addTodo="addTodo"></MyHeader>
        <List :todos="todos" :checkTodo="checkTodo" :delTodo="delTodo"></List>
        <MyFooter
          :todos="todos"
          :clearTodo="clearTodo"
          :allDone="allDone"
        ></MyFooter>
      </div>
    </div>
  </div>
</template>

<script>
import MyHeader from "./components/MyHeader.vue";
import List from "./components/List.vue";
import MyFooter from "./components/MyFooter.vue";
export default {
  name: "App",
  data() {
    return {
      todos: JSON.parse(localStorage.getItem("todos")) || [],
    };
  },
  components: {
    MyHeader,
    List,
    MyFooter,
  },
  watch: {
    todos: {
      deep:true,
      handler(val) {
        localStorage.setItem("todos", JSON.stringify(val));
      },
    },
  },
  methods: {
    //添加一个todo
    addTodo(todoObj) {
      this.todos.unshift(todoObj);
    },
    //勾选or取消勾选一个todo
    checkTodo(id) {
      this.todos.forEach((todo) => {
        if (todo.id === id) todo.done = !todo.done;
      });
    },
    //删除一个todo
    delTodo(id) {
      this.todos = this.todos.filter((todo) => {
        return todo.id !== id;
      });
      console.log(id);
    },
    //MyFooter使用的函数全部清除todo
    clearTodo() {
      this.todos = this.todos.filter((data) => {
        return data.done !== true;
      });
    },
    //MyFooter全选
    allDone(bours) {
      this.todos.forEach((todo) => {
        bours ? (todo.done = true) : (todo.done = false);
      });
    },
  },
};
</script>

<style>
body {
  background: #fff;
}
.btn {
  display: inline-block;
  padding: 4px 12px;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0, 2),
    0 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
}
.btn-danger {
  color: #fff;
  background-color: #da4f49;
  border: 1px solid #bd362f;
}
.btn-danger:hover {
  color: #fff;
  background-color: #bd362f;
}
.btn:focus {
  outline: none;
}
.todo-container {
  width: 600px;
  margin: 0 auto;
}
.todo-container .todo-wrap {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}
</style>